<template>
    <el-dialog title="" :visible.sync="cancelLeaveDialogVisible" :width="dialogWidth" center :before-close="closeLeaveDialog">
        <i class="el-icon-question"></i>
        <span style="text-align:center"></span>
        <span slot="footer">
            <el-button @click="closeLeaveDialog" style="margin-right:10%"></el-button>
            <el-button type="danger" @click="logOut"></el-button>
        </span>
  </el-dialog>  
</template>

<script>
import { removeToken } from '../../../utils/cookie'
import { logout } from '../../../api/login'

export default {
    data() {
        return {

        }
    },

    props:{
        cancelLeaveDialogVisible: { type: Boolean, default: false },
        dialogWidth: { type: String, default: '' },
    },

    methods:{
        closeLeaveDialog: function(){
            this.$emit('cancelLeaveDialogVisible', false)
        },

        logOut: function(){
            this.$message({ message: '', type: 'info' });
            logout().finally( () => {removeToken()});
            this.$router.push({name: 'Login'})
            this.closeLeaveDialog()
        },
    }
}
</script>